}

.map__content {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: flex-start;
}

.map__icon {
  height: 2rem;
}

.map__title {
  font-size: 2.14286rem;
  line-height: 2.71429rem;
  padding-top: 3.71429rem;
  font-family: 'Lato';
  font-weight: 900;
  font-style: normal;
  text-transform: uppercase;
}

.map__title-continue {
  font-size: 2.14286rem;
  line-height: 2.71429rem;
  padding-bottom: 3.71429rem;
  font-family: 'Lato';
  font-weight: 400;
  font-style: normal;
  text-transform: uppercase;
}

.map__list {
  display: flex;
  flex-direction: column;
  padding-bottom: 15.71429rem;
}

.map__item {
  display: flex;
  align-items: center;
}

.map__img-wrap {
  width: 3rem;
  height: 2rem;
}

.map__desc {
  font-size: 1.14286rem;
  line-height: 2.71429rem;
  font-family: 'Lato';
  font-weight: 900;
  font-style: normal;
}

/* Стили секции map */

/* Cтили блока buttons */

.buttons {
  padding: 1.78571rem 7.14286rem;
  height: 5.71429rem;
  border-radius: 50px;
  background: linear-gradient(127deg, #fe7f7d, #fe667d);
  box-shadow: 0 14px 20px rgba(0, 0, 0, 0.2);
  font-size: 1.14286rem;
  line-height: 1.21429rem;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  font-family: 'Lato';
  font-weight: 100;
  font-style: normal;
}

/* Конец блока buttons */

/* Cтили блока nav */

.nav__list {
  display: flex;
}

.nav__item {
  margin-left: 3.21429rem;
  line-height: 1.78571rem;
  font-size: 1rem;
  text-transform: uppercase;
  position: relative;
  z-index: 10 !important;
  cursor: pointer;
}

.nav__item::before {
  content: "";
  position: absolute;
  width: 85%;
  top: 1.14286rem;
  left: -0.35714rem;
  height: 0.64286rem;
  background-color: #aa4f93;
  z-index: -100;
  box-shadow: 0 9px 20px rgba(0, 0, 0, 0.2);
  opacity: 0;
  transition: all .5s;
}

.nav__item:hover::before {
  opacity: 1;
  transition: all .5s;
}

/* Конец блока nav */

@media (max-width: 1200px) {
  .container {
    padding: 0 10px;
  }
}

@media (max-width: 960px) {
  html,
  body {
    font-size: 12px;
  }
}